import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { AppBar, Toolbar, ImageList, ImageListItem } from '@mui/material';
import Modal from 'react-modal';
import Button from '@mui/material/Button';

const Search = () => {
  const [data, setData] = useState([]);
  const [page, setPage] = useState(1);
  let { value } = useParams();
  const [loading, setLoading] = useState(false);
  const [total, setTotal] = useState('');
  const [selectedImage, setSelectedImage] = useState(null);

  const openModal = (image) => {
    setSelectedImage(image);
  }

  const closeModal = () => {
    setSelectedImage(null);
  }

  const loadMoreImages = async () => {
    setLoading(true)
    await axios.get(`https://pixabay.com/api/?key=39835288-9d7d47305bde5faa0a7c1c2b3&q=${value}&image_type=photo&per_page=12&page=${page + 1}`)
      .then(res => {
        setData([...data, ...res.data.hits]);
        setPage(page + 1);
        setLoading(false)
      })
      .catch(error => {
        console.log('Error fetching images:', error);
        setLoading(false)
      });
  }

  useEffect(() => {
    axios.get(`https://pixabay.com/api/?key=39835288-9d7d47305bde5faa0a7c1c2b3&q=${value}&image_type=photo&per_page=12`)
      .then(res => {
        setData(res.data.hits);
        setTotal(res.data.total);
      })
      .catch(error => {
        console.log('Error fetching images:', error);
      });
  }, [value]);

  return (
    <div>
      <AppBar style={{ 'position': "absolute", "margin": 0 }}>
        <Toolbar><h1>{value}:{total}</h1></Toolbar>
      </AppBar>
      {data.length >= 7 ?
        <div id="container">
          <ImageList rowHeight={170} cols={3}>
            {data.map((img, index) => (
              <center key={index}>
                <div id="image-wrapper" onClick={() => openModal(img.largeImageURL)}>
                  <ImageListItem cols={1}>
                    <img src={img.largeImageURL} alt={`photo-${index}`} />
                  </ImageListItem>
                  <p>{img.tags}</p>
                </div>
              </center>
            ))}
          </ImageList>
        </div> : <center><h1 style={{ "margin": 100 }}>Loading...</h1></center>}
      <center>
        <hr></hr>
        <button
          className={loading ? 'show-more-button loading' : 'show-more-button'}
          onClick={loadMoreImages}
        >
          {loading ? 'Loading...' : ''}
        </button>
      </center>

      <Modal
        isOpen={selectedImage !== null}
        onRequestClose={closeModal}
        contentLabel="Image Modal"
        style={{
          overlay: {
            backgroundColor: 'rgba(0, 0, 0, 0.5)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            

          },
          content: {
            width: '70%',
            maxWidth: '800px',
            background: 'transparent',
            borderRadius: '8px',
            height:'400px',
            boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
            outline: 'none',
            border:"none",
            
          
          
          }
        }}
      >
        {selectedImage && <img src={selectedImage} alt="Selected" style={{width:"100%"}} />}
        <Button variant="contained" style={{"position":"absolute","right":20,"top":20,"width":70,"height":30}} onClick={closeModal}>Close</Button>
      </Modal>
    </div>
  )
}

export default Search;
